import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import seedColors from './seedColors';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import { blue } from '@material-ui/core/colors';
import { red } from '@material-ui/core/colors';
import MiniPalette from './MiniPalette';
import { withStyles } from '@material-ui/styles';
import styles from './styles/PaletteListStyles';


 class PaletteList extends Component {
     constructor(props){
         super(props);
         this.state = { openDeleteDialog: false, deletingId:"" }
         this.openDialog = this.openDialog.bind(this);
         this.closeDialog = this.closeDialog.bind(this);
         this.handleDelete = this.handleDelete.bind(this);
         this.goToPalette = this.goToPalette.bind(this);
     }
     
     openDialog(id){
        this.setState({ openDeleteDialog: true, deletingId:id })
    }

     closeDialog(){
         this.setState({ openDeleteDialog: false, deletingId:"" })
     }
     goToPalette(id){
       return  this.props.history.push(`/palette/${id}`);
     }
     handleDelete(){
        this.props.deletePalette(this.state.deletingId);
        this.closeDialog();
    }
    render() {
    
        const { palettes, classes } = this.props;
        const { openDeleteDialog } = this.state;
        return (
            <div className={classes.root}>
                <div className={classes.container}>
                    <nav className={classes.nav}>
                        <h1 className={classes.heading}>React Colors</h1>
                        <Link to="/palette/new"><h3>Create palette </h3></Link>
                    </nav>
                        <TransitionGroup className={classes.palettes} > 
                            { palettes.map(palette =>(
                                <CSSTransition key={palette.id} classNames="fade" timeout={500} >
                                    <MiniPalette 
                                    openDialog={this.openDialog} 
                                    id={palette.id} 
                                    key={palette.id} 
                                    {...palette} 
                                    goToPalette={this.goToPalette} 
                                    />
                                </CSSTransition>
                            )) }
                        </TransitionGroup>
                </div>

    <Dialog onClose={this.closeDialog} aria-labelledby="delete-dialog-title" open={openDeleteDialog}>
      <DialogTitle id="delete-dialog-title">Delete This Palette? </DialogTitle>
      <List>
          <ListItem button onClick={this.handleDelete} >
            <ListItemAvatar>
              <Avatar style={{ backgroundColor: blue[100], color:blue[600] }} >
                <CheckIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary="Delete" />
          </ListItem>
          <ListItem button onClick={this.closeDialog} >
            <ListItemAvatar>
              <Avatar style={{ backgroundColor: red[100], color:red[600] }}>
                <CloseIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary="Cancel" />
          </ListItem>
      </List>
    </Dialog>
               </div>
        )
    }
}

export default withStyles(styles)(PaletteList);
