import chroma from 'chroma-js';
import sizes from './sizes';

export default {
    ColorBox:{
        width:"20%",
        height: props=> props.showFullColors ? "25%" :"50%",
        margin: "0 auto",
        display: "inline-block",
        position: "relative",
        cursor: "pointer",
        marginBottom: "-3.5px",
        "&:hover button":{
            opacity:1,
            transition:"0.5s"
        },
        [sizes.down("lg")]:{
            width: "25%",
            height:props=> props.showFullColors ? "20%" :"33.3333%"
        },
        [sizes.down("md")]:{
            width: "50%",
            height:props=> props.showFullColors ? "10%" :"20%"
        },
        [sizes.down("xs")]:{
            width: "100%",
            height:props=> props.showFullColors ? "5%" :"10%"
        }
    },
    copyText:{
        color: props=> chroma(props.background).luminance() <= 0.08 ? "white" : "black"
    },
    colorName:{
        color: props=> chroma(props.background).luminance() <= 0.08 ? "white" : "black"
    },
    copyButton:{
        color: props=> chroma(props.background).luminance() >= 0.7 ? "rgba(0,0,0,0.6)" : "white",
        width:"100px",
        height:"30px",
        position:"absolute",
        top: "50%",
        left:"50%",
        marginLeft: "-50px",
        marginTop: "-15px",
        textAlign: "center",
        outline: "none",
        backgroundColor:" rgba(255,255,255, 0.3)",
        fontSize: "1rem",
        lineHeight: "30px",
        textTransform: "uppercase",
        border:"none",
        textDecoration: "none",
        opacity:0
    },
    seeMore:{
        color: props=> chroma(props.background).luminance() >= 0.7 ? "rgba(0,0,0,0.6)" : "white",
        position: "absolute",
        right: "0px",
        bottom:"0px",
        backgroundColor:  "rgba(255,255,255, 0.3)",
        width: "60px",
        height: "30px",
        lineHeight: "30px",
        textAlign: "center",
        textTransform: "uppercase"
    },
    boxContent:{
        position: "absolute",
        width: "100%",
        bottom:"0px",
        left:"0px",
        padding:"10px",
        color: "black",
        letterSpacing: "1px",
        textTransform: "uppercase",
        fontSize: "12px"
    },
    copyOverlay:{
        opacity: "0",
        zIndex: "0",
        width: "100%",
        height: "100%",
        transition: "transform 0.6s ease-in-out",
        transform: "scale(0.1)"
    },
    showOverlay:{
        opacity: "1",
        transform: "scale(50)",
        zIndex: "10",
        position: "absolute"
    },
    copyMessage:{
        position:"fixed",
        top: "0px",
        left: "0px",
        bottom: "0px",
        right: "0px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        fontSize: "4rem",
        transform: "scale(0.1)",
        opacity:" 0",
        color:"white",
        "& h1":{
            fontWeight: "400",
            textShadow: "1px 2px black",
            background: "rgba(255, 255, 255, 0.2)",
            width: "100%",
            textAlign: "center",
            marginBottom: "0",
            padding: "1rem",
            textTransform: "uppercase",
            [sizes.down("xs")]:{
                fontSize: "6rem"
            }            
        },
        "& p":{
            fontSize: "2rem",
            fontWeight: "100"
        }
    },
    showMessage:{
        transform: "scale(1)",
        zIndex: "25",
        opacity: "1",
        transition: "all 0.4s ease-in-out",
        transitionDelay: "0.3s"
    }

}