import chroma from 'chroma-js';
import sizes from './sizes';
const styles = {
    root:{
        width:"20%",
        height:"25%",
        margin: "0 auto",
        display: "inline-block",
        position: "relative",
        cursor: "pointer",
        marginBottom: "-6px",
        "&:hover svg":{
            color:"white",
            transform:"scale(1.5)"
        },
        [sizes.down("lg")]:{
            width:"25%",
            height:"20%"
        },
        [sizes.down("md")]:{
            width:"50%",
            height:"10%"
        },
        [sizes.down("sm")]:{
            width:"100%",
            height:"5%"
        }
    },
    boxContent:{
        position: "absolute",
        width: "100%",
        bottom:"0px",
        left:"0px",
        padding:"10px",
        color: props=> chroma(props.color).luminance() <= 0.08 ? "rgba(255,255,255,0.8)" : "rgba(0,0,0,0.6)",
        letterSpacing: "1px",
        textTransform: "uppercase",
        fontSize: "12px",
        display:"flex",
        justifyContent: "space-between",
        [sizes.down("sm")]:{
            padding:"0px",
            paddingBottom:"5px"
        }
    },
    deleteIcon:{
        fontSize:"1.2rem",
        transition: "all 0.5s ease-in-out"
    }
}

export default styles;